.poster{
    display:grid !important;
    padding: 20px;
    border-radius:20px;
    margin: 50px;
    min-height:200px;
    background: url(https://images.pexels.com/photos/414171/pexels-photo-414171.jpeg?cs=srgb&dl=pexels-pixabay-414171.jpg&fm=jpg)center no-repeat;
    cursor: pointer;
}

.poster__info{
    max-width: 350px;
    max-height: 150px;
    display: grid !important;
    align-content: center;
}

.poster__heading{
        font-size: 40px;
        max-width: 350px;
        padding: 10px;
        font-weight: 500;
       color: #0e0909;
       font-family: 'Segoe UI', Tahoma, Geneva, Verdana, sans-serif;
      text-align: left;
    }

 .poster__description{
     margin-bottom: 20px;
     font-size: 18px;
     font-family: Arial, Helvetica, sans-serif;
    text-align: left;
    margin-left: 15px;
 }   

 .poster__info > Button {
     left: 50px;
     background-color: black;
     color: whitesmoke;
     width: 250px;

 }