.banner{
    top:0%;
    background:url(https://images.pexels.com/photos/271624/pexels-photo-271624.jpeg?auto=compress&cs=tinysrgb&dpr=2&h=650&w=940) center  no-repeat;
    min-height: 550px;
   
}

.banner__info{
    background-color: black;
    padding:50px;
    color:whitesmoke;
    width: 20%;
    margin-top: 20px;
}

.banner__info > Button{
      color: black;
      background-color:rgb(255,91,97);
}


 
.banner__info > Button:hover{
    background-color:wheat;
    color:black;
}

.banner__content{
    display: flex;
  }

.header__icon{
    width:150px;
}

.header{
    top:0%;
    display:flex;
    align-items:center;
    max-width: 100%;
    justify-content:space-around;
   position: relative;
   
}

.header__center{
    display: flex !important;
    height: 60px!important;
    width: 850px;
    max-width:fit-content;
    top: 100px;
    position: relative !important;
       background-color: #FFFFFF !important;
       border: 1px solid #DDDDDD !important;
        border-radius: 999px;
        margin-left: 75px;
        box-shadow: 0px 16px 32px rgb(0 0 0 / 15%), 0px 3px 8px rgb(0 0 0 / 10%) !important;
        justify-content:space-between;
}

.header__center__box{
    width: 100%;
    display: flex !important;
    flex: 1 1 0% !important;
    height: 100% !important;
    min-width: 0px !important;
    pointer-events: auto !important;
    margin-left: 20px !important;
    top:0%;
}


.header__right{
    display: flex;
    justify-content: space-between;
    align-items: center;
    width:300px;
}

.header__right > Button{
    width: 140px;
    
}

.language__icon{
    border: 2px solid gray;
    padding:3px;
    border-radius: 999px;
}

.search__icon{
    padding:10px;
    background-color: rgb(254,94,99);
    border-radius :999px;
    margin-top: -2px;
    margin-right: 10px;
}

.searchbox{
     align-items: center;
     height: 60px;
     width:180px;
     border-radius: 999px;
     display: flex !important;
     justify-content:space-around;
}

.searchbox:hover{
    background-color:rgb(235,235,235);
}

.checkout{
    align-items: center;
    height: 60px;
    width:180px;
    border-radius: 999px;
    display: flex !important;
    justify-content:space-around;
}

.checkin{
    align-items: center;
    height: 65px;
    width:fit-content;
    display: flex !important;
    z-index: 1 !important;
    margin-left: 20px;
    justify-content:space-around;
}

.place{
    align-items: center;
    height: 65px;
    width:fit-content;
    display: flex !important;
    z-index: 1 !important;
    margin-left: 20px;
    justify-content:space-around;
}


.searchbox__heading {
    font-size:16px;
   font-family: 'Courier New', Courier, monospace;
}

.searchbox__info {
   height: 40px;
   width:150px;
    top: 0%;
    border:0;
    align-items: center;
    display:grid;
    padding: 10px;
    border-radius: 999px;
    
}



.searchbox__info > input {
    border: 0px solid black !important;
    outline-width: 0px;
    padding: 0px !important;
    width: 100px !important;
    background: none !important;
    font-size: 12px !important;
    line-height: 18px !important;
    font-weight: 600 !important;
    color: #222222 !important;
    text-overflow: ellipsis !important;
}

.divider {
    padding: 15px;
    margin-top:3px;
}

